/**
 * ajaxret=0
 * @access public
 * @param {String | Object} url urlt
 * @param {Function} [success] , BooleantruelocalStoragefalse,
 *                         cache
 * @param {Function} [error] 
 * @param {Function} [timeOut] 
 * @param {string} [options.cache=""]     
 * <p>""             </p>
 * <p>"normal_local"  localStorage</p>
 * <p>"next_local    render localStorage</p>
 * <p>"normal_session  sessionStorage</p>
 * <p>"next_session    render sessionStorage</p>
 * @return {Function}  Promise
 */

//cache
var _fetch = function(options) {
    var cache = options.cache || "";
    var defer = $.Deferred();

    if(BCD.is.s(options)){
      options = {
        url: options
      };
    }
    BCD.ajaxCache($.extend({
        timeout: 6e3,
        xhrFields: {
            withCredentials: true
        },
        useCache: cache.indexOf('next') === 0,
    }, options, {
        success: function(data){
            try{
                defer.resolve(data);
                var isRight;
                var storage = cache && (cache.indexOf('local') > 0 ? 1 : 2);
                if(options.success){
                    isRight = options.success(data);
                    if(isRight){
                        return storage;
                    }
                }else{
                    isRight = data && data.ret===0;
                }
                return isRight !== false && storage;
            }catch(e){
                console.log(e, 'promise_ajax fetch url "' + options.url + '", has wrong in success');
            }
        },
        error: function(){
            try{
                console.log(options.url + ' promise_ajax error');
                defer.resolve(false);
                if(options.errors){
                    options.errors.apply(this, arguments);
                }
            }catch(e){
                console.log(e, 'promise_ajax fetch url "' + options.url + '", has wrong in error');
            }
        }
    }));
    return defer.promise();
};

function batchFetch(urls, option){
    var promiseList = (urls || []).map(function(url){
        return _fetch($.extend({
            url: url
        }, option));
    });
    return $.when.apply(promiseList, promiseList).then(function(){
        return arguments;
    });
}
//
module.exports = {
    fetch: _fetch,
    batchFetch: batchFetch
};



